import React from "react"
import cover1 from "../../images/detail/cover1.jpg"
import cover2 from "../../images/detail/cover2.jpg"
import cover3 from "../../images/detail/cover3.jpg"




const Content = () => {

  return (
    <div className="detail-content">
        <div className="detail-content-grid">
            <img src={cover1} alt="" />
            <img src={cover2} alt="" />
            <img src={cover3} alt="" />
        </div>
    </div>
  )
}

export default Content