import React from "react"
import { Button } from "react-bootstrap"

const Sidebar = () => {


  return (
    <div className="detail-sidebar">
        <div className="detail-sidebar-sticky">
        
        <div className="detail-sidebar-title">
            <h2>Expanded Gender identities</h2>
            <div className="posted">
                <ul>
                    <li>Bumble</li>
                    <li>2019</li>
                </ul>
            </div>
           <div className="category-tag">
            <ul>
                <li>Product Design</li>
                <li>GENDER</li>
            </ul>
           </div>
        </div>


        <div className="detail-sidebar-content">
            <h4>Overview</h4>
            <p>Bumble partnered with GLAAD, a media advocacy organization advancing LGBTQ+ acceptance, to expand the number of gender identities and sexual orientations available to users.</p>
            <p>There are now over 30 gender identity and expressions options to select from (up from 3), so that they can better express themselves in ways that reflect who they truly are.  </p>

            <h4>Why it matters</h4>
            <p>Questions like, "Will my date respect me?", "Should I wear makeup to appear more feminine?", and "Should I go home with him?" can make any dater nervous, regardless of gender identity. But as a non-binary person, these questions are a matter of life and death.</p> 
            <p>The FBI's 2018 Hate Crime Statistics Report found that one in five confirmed hate crimes committed in 2018 were motivated by anti-LGBTQ bias. Transphobic violence made up about 14% of the anti-LGBTQ incidents, and 2.4% of all hate crimes. To make things worse, gay or trans panic is widely considered a legitimate legal defense to excuse cis violence against trans people</p>

        </div>

        <div className="take-action">
            <h4>Learn more</h4>
            <ul>
                <li>Designing forms for gender diversity and inclusion - Sabrina Fonseca</li>
                <li>Principles for Inclusive Gender Inputs: How League went Beyond Binaries - Demi</li>
                <li>Gender-inclusive languages: A (linguistic) journey around the world - Preply</li>
                <li>Tips for designing inclusive forms</li>
            </ul>
        </div>

        <div className="take-action">
            <h4>Take action</h4>
            <ul>
                <li>Gender Inclusive Form Components - Figma</li>
                <li>Inclusive Components - Inclusive Components</li>
                <li>Inclusive Langauge Guidelines - APA</li>
            </ul>
        </div>

        <div className="take-action">
            <h4>Contribute</h4>
            <ul>
                <li>Suggest a resource</li>
                <li>Suggest an edit</li>
            </ul>
        </div>


        <div className="newsletter">
            <h4>New examples each week</h4>
            <p>Join “Resonate”, my weekly series that puts the best examples, tips, and insights for designing products that resonate with everyone, everywhere.</p>
            <Button>Join the newsletter</Button>
        </div>
        </div>
    </div>
  )
}

export default Sidebar